.root {
  height: 100%;
  display: flex;
  flex-direction: row;
}
.root .menu {
  width: 200px;
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
}
.root .menu .productName {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80px;
  background: #ccc;
}
.root .content {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  padding: 10px;
  overflow-y: auto;
  overflow-x: auto;
  background: #f3f8fb;
}
.root .content .contentHeader {
  flex: 0 0 auto;
  height: 60px;
  padding: 10px;
  background: #fff;
  margin-bottom: 10px;
  min-width: 1040px;
}
.root .content .contentContainer {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-width: 1040px;
  padding: 10px;
  background: #fff;
}
.root .content .contentContainer .childContent {
  flex: 1 1 auto;
  width: 100%;
  box-sizing: content-box;
}
.title {
  color: red;
