.root {
  box-shadow: 0 0 10px -4px #ccc;
  margin-bottom: 10px;
  width: 600px;
  padding: 10px;
  box-sizing: content-box;
  overflow: hidden;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;
}
.avatar {
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-size: cover;
}
.content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 10px;
}
.content .header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 60px;
}
.content .header .more {
  position: absolute;
  right: 0;
  top: 0;
}
.content .images {
  width: 360px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}
.content .images .image {
  width: 110px;
  height: 110px;
  margin-right: 10px;
  margin-top: 10px;
  cursor: pointer;
}
.previewImg {
  max-width: 100%;
}
.modalNavi {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 10px;
}
.modalNavi .naviImage {
  width: 50px;
  height: 50px;
  margin-right: 5px;
  position: relative;
}
.modalNavi .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  background-color: #000000;
  opacity: 0.4;
  cursor: pointer;
}
.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 3px 0;
}
.footer .perItem {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.footer .perItem > span {
  margin-left: 5px;
}



