.root {
  display: flex;
  flex-direction: column;
}
.root .perMenu {
  display: flex;
  flex-direction: column;
}
.root .perMenu .menuTitle {
  height: 56px;
  padding-left: 30px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
}
.root .perMenu .menuTitle:hover {
  color: #37bd78;
  fill: #37bd78;
}
.root .perMenu .menuTitle div:first-child {
  margin-right: 10px;
}
.root .perMenu .menuChild {
  display: flex;
  flex-direction: row;
  padding-left: 30px;
}
.root .perMenu .menuChild .childLine {
  margin-left: 10px;
  width: 2px;
  background: #f2f2f2;
}
.root .perMenu .menuChild .childContainer {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}
.root .perMenu .menuChild .childContainer .perChild {
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  cursor: pointer;
}
.root .perMenu .menuChild .childContainer .perChild:hover {
  color: #37bd78;
  fill: #37bd78;
}



